:root
  --greenTitleColor: #198754
  --yellowPColor: #F5B935
  --roundedCorners: 20px
  --whiteMainTextColor: #ffffff
  --grayExtraTextColor: #6C7589
  --redErrorColor: #DF3062
  --deepBlackColor: #0F0C24

*
  box-sizing: border-box
  margin: 0px
  padding: 0px
  border: none
body
  display: grid
  grid-template-rows: 60px, auto

a:hover, a:visited, a:link, a:active

  text-decoration: none

h1
  color: var(--greenTitleColor)
  font-family: 'Roboto', sans-serif
  font-weight: 900
  font-size: 60px

  // margin-bottom: 5%

h2
  color: var(--whiteMainTextColor)
  font-family: 'Roboto', sans-serif
  font-weight: 700
  font-size: 48px

  margin-bottom: 3%

h3
  color: var(--redErrorColor)
  font-family: 'Montserrat', sans-serif
  font-weight: 500
  font-size: 25px

  margin-bottom: 5%

p
  color: var(--yellowPColor)
  font-family: 'Montserrat', sans-serif
  font-weight: 400
  font-size: 16px

  margin-bottom: 2%

h4, h5, h6
  color: var(--whiteMainTextColor)
  font-family: 'Montserrat', sans-serif
  font-weight: bold
  font-size: 20px

  margin-bottom: 2%

li
  color: var(--yellowPColor)
  font-family: 'Montserrat', sans-serif
  font-weight: 400
  font-size: 16px

  margin-bottom: 2%

input, button
  border-radius: var(--roundedCorners)
  font-family: 'Montserrat', sans-serif
  font-weight: bold
  font-size: 20px
  outline: none

  padding: 10px

  min-width: 20px

  min-width: 150px
  margin-bottom: 3%
.categories
  input
    width: 30px
    height: 30px
button
  margin-top: 1%
  margin: 1% 1%
  height: 60px
  font-family: 'Montserrat', sans-serif
  font-weight: 500
  font-size: 25px
  font-weight: bold
  background-color: var(--greenTitleColor)
  color: var(--whiteMainTextColor)

  &:hover
    transform: translateY(-3px)
    background-color: #18724b
    transition: 0.5s

  &:not(:hover)
    transition: 0.5s

  &:focus
    outline: 0

body
  background-image: url("img/2.png")

